<template>
  <div class="sidebar col1" >
    <h1>Sidebar</h1>
    <Past/>
    <Account/>
    <EmptyCart/>
    <!-- <ul>
      <button
        v-for="tab in tabs"
        v-bind:key="tab.name"
        v-bind:class="['tab-button', { active: currentTab.name === tab.name }]"
        v-on:click =""
        >{{ tab.name }}
      </button>
    </ul> -->
    
    <ul class = "cart">
      <li class = "" 
        v-for="(item, index) in cartItems"
        v-bind:key="item.id">{{item.name}} <br class="priceBreak"/> + {{ item.price }}
        <button v-on:click="removeItem(item, index)">x</button>
      </li>
    </ul> 

    <Order/>
  </div>
</template>
  
<script>
  import{mapActions} from 'vuex'
  import Order from './buttons/Order'
  import Past from './buttons/Past'
  import Account from './buttons/Account'
  import EmptyCart from './buttons/EmptyCart'

  export default {
    name: 'Sidebar',
    data(){
      return{
        tabs: tabs,
        currentTab: tabs[0]
      };
    },
    components: {
      Past,
      Account,
      EmptyCart,
      Order
    },
    computed:{
      cartItems(){
        var content = this.$store.getters.cart.content;
        return content
      }
    },
    methods: {  
      getSpecialMenu: function(){
        console.log("retrieveSpecialist")
          this.$store.dispatch('retrieveSpecialist')
      },
      testFunc: function(){
        console.log('test')
      },
      removeItem: function(item, index){
        var args = {item, index}
        this.$store.dispatch('removeFromCart', args)
      }
    },
    created(){
    }
  }

var tabs = [
  // {
  //   name: 'Past Orders', 
  // },
  // {
  //   name: 'Order Now', 
  // },
  // {
  //   name: 'Address',
  // },
  // {
  //   name: 'Empty Cart', 
  // },
  // {
  //   name: 'Sign In',
  // }
]
</script>

<style scoped>
.tab-button{
  width:100%;
}

.priceBreak{
  /* display: none; */
}
  
.cart li{
  display: block;
}

li{
  list-style: none;
  /* color: white; */
}

a{
  text-decoration: none;
}

.sidebar{
  /* background-color: rgb(247, 247, 247); */
  border-top: 3px solid black;
  border-bottom: 3px solid black;
  grid-column-start: 1;
  grid-column-end:   3;
  grid-row-start:    2;
  grid-row-end:      11;
  box-sizing: border-box;
  padding: 25px;
  /* font-size: 1vi; */
  /* background-color: rgb(255, 252, 225); */
}

h1{
  margin-top: 0px;
}

@media(max-width: 640px){
  .priceBreak{
    display: none;
  }

  ul{
    width: 25%;
    float: left;
  }
  li{
    list-style: none;
    display: inline;
  }

  .sidebar{
    grid-column-start: 1;
    grid-column-end:   11;
    grid-row-start:    2;
    grid-row-end:      3;
  }

  .cart{
    /* width:25%; */
    width:100%;
    float: right;
  } 
}
</style>



